<template>
  <div>
    <component @upload-start="uploading = true" @upload="uploadFile" @back-click="currentComponent = 'formUpload'" @on-recorder-selected="showRecorder"
               :is="currentComponent"></component>
    <div v-if="uploading">
      <v-progress-circular :indeterminate="isInfinite"
                           :color="'#0101d5'"></v-progress-circular>
      <br>
      <br>
      <p class="message">Predicting</p>
    </div>

  </div>
</template>

<script>
    import $backend from '../backend'
    import FormUpload from './FormUpload'
    import AudioRecorder from './AudioRecorder'

    export default {
        name: "AudioUploadOptions",
        components: {
            formUpload: FormUpload,
            audioRecorder: AudioRecorder,
        },
        data() {
            return {
                uploading: false,
                uploadProgress: 0,
                currentComponent: 'formUpload',
                isInfinite: true,
            }
        },

        methods: {
            showRecorder() {
                this.currentComponent = 'audioRecorder'

            },
            uploadFile(response) {
                this.$emit('upload', response);

            }
        }
    }
</script>

